import React from "react"
import reactDom from "react-dom"
import { Link } from "react-router-dom"
import Navitem from "./Navitem"

class NavBar extends React.Component{
    constructor(props){
        super(props);
        this.state={
            navitemactive:""
        }
    }
    activeitem =(x)=> {
        if(this.state.navitemactive>0){
            document.getElementById(this.state.navitemactive).classList.remove("active");
        }
        this.setState({'navitemactive':x},()=>{
            document.getElementById(this.state.navitemactive).classList.add("active");
        })
    }
    render(){
        return (
            <nav>
                <ul>
                    <Navitem item="Home" tolink="/" activelink={this.activeitem}></Navitem>
                    <Navitem item="About" tolink="/about" activelink={this.activeitem}></Navitem>
                    <Navitem item="Education" tolink="/education" activelink={this.activeitem}></Navitem>
                    <Navitem item="Skill" tolink="/skill" activelink={this.activeitem}></Navitem>
                    <Navitem item="Contact" tolink="/contact" activelink={this.activeitem}></Navitem>
                </ul>
            </nav>
        )
    }
}

export default NavBar;