import React, {Component} from "react"
class Skill extends Component{
    constructor(props){
        super(props);
        this.state={
            "skills":["PHP Codeigniter","Python Django","Postgresql","SQL","React","Angular js","Javascript",,"Bootstrap","CSS",
            "AWS","Hosting -cpanel"]
        };
    }
    render(){
        return(
            <div className="condiv skills">
<h1 className="subtopic">My Skills</h1>
                <ul>
                    {this.state.skills.map(
                        (value)=>{
                            return <li>{value}</li>
                        }
                    )}
                </ul>
            </div>
        )
    }
}
export default Skill